body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  width: 100%;
}


.warCouncil{
  justify-content: center;
}

.Menu{
    display: flex;
    width: 100%;
   justify-content: space-between;
   background-color: #ffe4b5;
   height: 90px; 
   padding-top: 1%;
}

h2{
    padding-right: 10%;
    padding-left:3%;
    background-color: #ffe4b5;
    height: 50px;

}

.menuItems{
    
    display: flex;
    justify-content: space-evenly;
    width: 500px;
    background-color: moccasin;
    margin-left: 20px;
}

.Avatar{
    width: 30px;
    background-color: transparent;
}

.loggedInMenu{
    display: flex;
}

.container{
    position: relative;
    width: 150%;
    padding-bottom: 50px;

}


.bannerText{
    position: absolute;
    top: 30%;
    left: 30%;
    width: 1000px;
    transform: translate(
        -50%, -50%);
    color: black;
    font-size: 600%;
}
.bannerImage{
    width:120%;
    margin-left: -10%;
    -webkit-filter: opacity(60%);
            filter: opacity(60%);
   
}

.description{
    height: 20%;
    padding-top: 5%;
    padding-bottom: 5%;
    width: 175.7%;
    Margin-left: -40%;
    padding-left: 40%;
    padding-right: 40%;
    font-size: 250%;
}

